import React from 'react';
import {Table, Button} from 'react-bootstrap';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';

function Cart(props){
    return (
        <div>
            
            <Table responsive="sm">
                <thead>
                <tr>
                    <th>#</th>
                    <th>상품명</th>
                    <th>수량</th>
                    <th>변경</th>
                </tr>
                </thead>
                <tbody>
                {
                    props.state.map((a,i)=>{
                        return (
                            <tr key={ i }>
                                <td>{ a.id }</td>
                                <td><Link to={'/detail/'+a.id}>{ a.name }</Link></td>
                                <td>{ a.quan }</td>
                                <td>
                                    <Button variant="info" onClick={()=>{
                                        if(props.remains[a.id] === 0){
                                            alert('No more item in stock!')
                                        }
                                        else {
                                            props.dispatch({ type : 'increase', number : i });
                                            let newremains = [...props.remains];
                                            newremains[a.id] -= 1;
                                            props.remainChange(newremains); 
                                        }
                                    }}>+</Button>
                                    <Button variant="danger" onClick={()=>{
                                        props.dispatch({ type : 'decrease', number : i });
                                        let newremains = [...props.remains];
                                        newremains[a.id] += 1;
                                        props.remainChange(newremains);
                                        if(a.quan === 0){
                                            props.dispatch({ type : 'delete', number : i })
                                        }
                                    }}>-</Button></td>
                            </tr>
                        )
                    })
                }
                </tbody>
            </Table>
            {
                props.alert
                ?   <div className="my-alert2">
                        <p>지금 구매하시면 50% 할인!</p>
                        <Button variant="light" onClick={()=>{ props.dispatch({ type : 'close' }) }}>닫기</Button>
                    </div>
                : null
            }
            
        </div>
    )
}

function stp(state){
    return {
        state : state.reducer,
        alert : state.reducer2
    }
}

export default connect(stp)(Cart)

